import React, { useState, useEffect, useRef } from 'react';
import DOTS from 'vanta/dist/vanta.dots.min';
import Herosection1 from './herosection1'
import { motion } from 'framer-motion';

const MyComponent = () => {
  const [vantaEffect, setVantaEffect] = useState(null);
  const myRef = useRef(null);

  useEffect(() => {
    if (!window.THREE) {
      // Ensure THREE is available on the window object
      console.error('THREE is not defined on the window object. Make sure three.min.js is included.');
      return;
    }

    if (!vantaEffect && myRef.current) {
      setVantaEffect(DOTS({
        el: myRef.current,
        // Add any additional Vanta.js options here
        // el: "#your-element-selector",
        mouseControls: true,
        touchControls: true,
        gyroControls: false,
        minHeight: 200.00,
        minWidth: 200.00,
        scale: 1.00,
        scaleMobile: 1.00,
        backgroundColor: 0x23153c,
        color: 0xff3f81,
        color2: 0xff3f81,
      }));
    }

    return () => {
      if (vantaEffect) vantaEffect.destroy();
    };
  }, [vantaEffect]);

  return (
    <div ref={myRef} style={{ width: '100%', height: '100vh'}} id='home'>.
    
      <Herosection1 />
      
    </div>
    
  );
}

export default MyComponent;
